#contactSection {
    min-height: 80vh;
    margin-bottom: 20px;
    position: relative;
}

#contactHeader {
    margin: 15px 0;
    font-size: 45px;
    color: white;;
    transition: ease-in 0.3s;
}

#contactContainer{
    width:80%;
    margin: 20px auto;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#contactContainerHead{
    font-size: 25px;
    margin: 20px 0;
}
#contactForm{
    display:flex;
    flex-direction: column;
    width:80%;
    min-width: 400px;
}
.inputContainer{
    position: relative;
    margin-top: 30px;
}
.inputContainer > input, .inputContainer > textarea{
    font: 14px "Lucida Grande", Helvetica, Arial, sans-serif;
    font-size: 25px;
    margin-bottom: 5px;
    border:0;
    padding:0;
    min-height:50px;
    width:100%;
    outline: none;
    color: white;
    background-color:#424242;
}
.contactLabel{
    color: white;
    position: absolute;
    top:10px;
    left:0;
    font: 14px "Lucida Grande", Helvetica, Arial, sans-serif;
    font-size: 25px;
    transition: ease-in 0.3s;
    -webkit-transition: ease-in 0.3s;
    -moz-transition: ease-in 0.3s;
    -o-transition: ease-in 0.3s;
}
.contactBar{
    position: relative;
    background-color: #424242;
    height:3px;
    width:100%;
    overflow: hidden;
}
.contactBar2{
    position: absolute;
    left:-100%;
    background-color: white;
    height:3px;
    width:100%;
    transition: ease-in 0.5s;
    -webkit-transition: ease-in 0.5s;
    -moz-transition: ease-in 0.5s;
    -o-transition: ease-in 0.5s;
}

#contactFormSendContainer{
    margin-top: 25px;
    display:flex;
    justify-content: flex-end;
    transition: ease-in 0.5s;
}
#contactFormSend{
    width:100px;
    height:50px;
    border: 1px solid black;
    color: white;
    background-color: #424242;
    transition: ease-in 0.5s;
}
#contactFormSend:hover{
    color:black;
    background-color: white;
}

#contactFormAfterSubmit{
    color:white;
    font-size: 18px;
}